import React from 'react'
import AssetAccordion from './AssetAccordion'
import { Row, Col, FormGroup, FormText } from 'reactstrap'
import 'swiper/css/swiper.css'
import { Eye, Users, CreditCard, Package, Server } from 'react-feather'
import '../../assets/scss/plugins/extensions/swiper.scss'
import img1 from '../../assets/img/slider/banner-31.jpg'
import img2 from '../../assets/img/slider/banner-22.jpg'
import img3 from '../../assets/img/slider/banner-23.jpg'
import img4 from '../../assets/img/slider/banner-24.jpg'
import img5 from '../../assets/img/slider/banner-25.jpg'
import Swiper from 'react-id-swiper'
import 'swiper/css/swiper.css'

const params = {
  effect: 'cube',
  grabCursor: true,
  autoplay: {
    delay: 2000,
    disableOnInteraction: true
  },
  cubeEffect: {
    shadow: true,
    slideShadows: true,
    shadowOffset: 20,
    shadowScale: 0.94
  },
  pagination: {
    el: '.swiper-pagination'
  }
}

class Portfolio extends React.Component {
  state = {
    assetShow: false,
    liabiltyShow: false,
    assetList: [],
    liabilityList: [],
    assetList1: [
      {
        id: 1,
        title: 'FD Account',
        content: (
          <div>
            <Row>
              <Col md="6" sm="12">
                <FormGroup className="form-label-group">Hey 1</FormGroup>
              </Col>
              <Col md="6" sm="12">
                <FormText>Hey 2</FormText>
              </Col>
            </Row>
          </div>
        )
      },
      {
        id: 2,
        title: 'Bank Details',
        content:
          'Jelly-o brownie marshmallow soufflé I love jelly beans oat cake. I love gummies chocolate bar marshmallow sugar plum.'
      },
      {
        id: 3,
        title: 'Property',
        content:
          'Pudding lollipop dessert chocolate gingerbread. Cake cupcake bonbon cupcake marshmallow. Gummi bears carrot cake bonbon cake.'
      },
      {
        id: 4,
        title: 'Mutual Fund',
        content:
          'Brownie sweet carrot cake dragée caramels fruitcake. Gummi bears tootsie roll croissant gingerbread dragée tootsie roll.'
      }
    ],
    liabilityList1: [
      {
        id: 1,
        title: 'Home Loan',
        content:
          'Pie dragée muffin. Donut cake liquorice marzipan carrot cake topping powder candy. Sugar plum brownie brownie cotton candy.'
      },
      {
        id: 2,
        title: 'ICICI Credi Card',
        content:
          'Jelly-o brownie marshmallow soufflé I love jelly beans oat cake. I love gummies chocolate bar marshmallow sugar plum.'
      }
    ]
  }

  componentWillMount() {
    this.getAssets()
  }

  getAssets = () => {
    //alert(this.state.assetShow)
    const user = JSON.parse(localStorage.getItem('logInUserData'))
    const { assetList, liabilityList } = this.state
    //const assetList = [];
    assetList.splice(0, assetList.length)
    liabilityList.splice(0, liabilityList.length)

    user.assets.forEach((e, i) => {
      console.log('ffffffffffffffff', i, e)
      let nomin = ''
      e?.nominees?.length &&
        e.nominees.forEach((n, idx) => {
          if (idx === e.nominees.length - 1) {
            nomin = nomin + n.name
          } else {
            nomin = nomin + n.name + ', '
          }
        })
      const cont = (
        <div>
          <Row>
            {e?.assetDetails?.map((x) => {
              if (x.val !== '') {
                console.log('dddddddddddddddddd', x.key, this.state.assetShow)
                return (
                  <Col md="6" sm="12">
                    <FormText>
                      <b>{x.key} :</b>{' '}
                      {this.state.assetShow ? x.val : '***********'}
                    </FormText>
                  </Col>
                )
              }
            })}
            <Col md="6" sm="12">
              <FormText>
                <b>Nominees :</b>{' '}
                {this.state.assetShow ? nomin : '*******************'}
              </FormText>
            </Col>
          </Row>
        </div>
      )
      const ast = {
        id: i + 1,
        title: e.assetType,
        content: cont
      }
      assetList.push(ast)
    })

    user.liabilities.forEach((e, i) => {
      console.log('ffffffffffffffff', i, e)
      let nomin = ''
      e.nominees.forEach((n, idx) => {
        if (idx === e.nominees.length - 1) {
          nomin = nomin + n.name
        } else {
          nomin = nomin + n.name + ', '
        }
      })
      const cont = (
        <div>
          <Row>
            {e.liabilityDetails.map((x) => {
              if (x.val !== '') {
                console.log(
                  'dddddddddddddddddd',
                  x.key,
                  this.state.liabiltyShow
                )
                return (
                  <Col md="6" sm="12">
                    <FormText>
                      <b>{x.key} :</b>{' '}
                      {this.state.liabiltyShow ? x.val : '***********'}
                    </FormText>
                  </Col>
                )
              }
            })}
            <Col md="6" sm="12">
              <FormText>
                <b>Nominees :</b>{' '}
                {this.state.liabiltyShow ? nomin : '*******************'}
              </FormText>
            </Col>
          </Row>
        </div>
      )
      const ast = {
        id: i + 1,
        title: e.liabilityType,
        content: cont
      }
      liabilityList.push(ast)
    })
    console.log(assetList)
    this.setState({
      assetList: assetList?.reverse(),
      liabilityList: liabilityList?.reverse()
    })
  }

  render() {
    return (
      <React.Fragment>
        <h2 className="warning spacing">Portfolio</h2>
        <Row>
          <Col lg="8" md="6" sm="12">
            <Row className="match-height">
              <Col md="12">
                <AssetAccordion
                  heading="Assets List"
                  // description="All your assets"
                  collapseItems={this.state.assetList}
                  //show={() => console.log('ddddddddddddd show called')}
                  show={() => {
                    this.setState({ assetShow: !this.state.assetShow })
                    this.state.assetShow = !this.state.assetShow
                    this.getAssets()
                  }}
                  assetShow={this.state.assetShow}
                  //show={() => {}}
                  page="portfolio"
                />
              </Col>
            </Row>
            <Row className="match-height">
              <Col md="12">
                <AssetAccordion
                  heading="Liabilities List"
                  // description="All your liabilities"
                  collapseItems={this.state.liabilityList}
                  show={() => {
                    this.setState({ liabiltyShow: !this.state.liabiltyShow })
                    this.state.liabiltyShow = !this.state.liabiltyShow
                    this.getAssets()
                  }}
                  assetShow={this.state.liabiltyShow}
                  page="portfolio"
                />
              </Col>
            </Row>
          </Col>
          <Col lg="4" md="6" sm="12">
            <div style={{ position: 'fixed', marginLeft: '60px' }}>
              <Swiper {...params}>
                <div
                  style={{
                    backgroundImage: `url(${img1})`,
                    backgroundSize: 'contain'
                  }}
                ></div>
                <div
                  style={{
                    backgroundImage: `url(${img2})`,
                    backgroundSize: 'contain'
                  }}
                ></div>
                <div
                  style={{
                    backgroundImage: `url(${img3})`,
                    backgroundSize: 'contain'
                  }}
                ></div>
                <div
                  style={{
                    backgroundImage: `url(${img4})`,
                    backgroundSize: 'contain'
                  }}
                ></div>
                <div
                  style={{
                    backgroundImage: `url(${img5})`,
                    backgroundSize: 'contain'
                  }}
                ></div>
              </Swiper>
            </div>
          </Col>
        </Row>
      </React.Fragment>
    )
  }
}

export default Portfolio
